<template>
  <div class='container'>
    <div class='player'>
      <svg width="100%" height="100%" preserveAspectRatio="none" viewBox='0,0,100,100'>
        <defs>
          <path id="path1" d="M10,30Q50 100 90 30" />
        </defs>
        <text x="10" y="100" style="fill:rgba(61, 151, 196, 0.67);">
          <textPath xlink:href="#path1">S w n b</textPath>
        </text>
      </svg>
      <video preload='auto' controls v-bind:src='src'>
      </video>
      <div>
      </div>
    </div>
    <div class=' list '>
      <div class='item '>
        <div>播放列表</div>
      </div>
      <div class='item ' v-for='ele in videoList ' data-w='add' v-bind:data-url='ele.url '>
        <div class='user widthreduce ' v-on:click='play '>{{ele.user}}</div>
        <div class='iconSpan ' v-on:click='changewidth '></div>
        <div class='filename widthadd ' v-on:click='play '>{{ele.filename}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'loadprogress',
  props: ['playing'],
  data: () => ({
    src: '',
    videoList: [],
    url: '/media/video',
    mediaUrl: '/video/',
    icon: true,
  }),
  methods: {
    getdata() {
      fetch(this.url).then(data => data.json()).then(data => {
        data = Object.values(data).map(d => {
          let tmp = {
            filename: d.filename,
            url: this.mediaUrl + d.fileTotalName,
            user: d.user,
          }
          return tmp
        })
        console.log(data);
        this.videoList = data
      })
    },
    changewidth(e) {
      let dom = e.target
      if (dom.parentElement.getAttribute('data-w') === 'add') {
        let [a, b] = [dom.parentElement.children[0], dom.parentElement.children[2]]
        a.classList.remove('widthreduce')
        b.classList.remove('widthadd')
        a.classList.add('widthadd')
        b.classList.add('widthreduce')
        dom.parentElement.setAttribute('data-w', 'reduce')
      } else if (dom.parentElement.getAttribute('data-w') === 'reduce') {
        let [b, a] = [dom.parentElement.children[0], dom.parentElement.children[2]]
        a.classList.remove('widthreduce')
        b.classList.remove('widthadd')
        a.classList.add('widthadd')
        b.classList.add('widthreduce')
        dom.parentElement.setAttribute('data-w', 'add')
      } else {
        dom.parentElement.setAttribute('data-w', 'add')
        setTimeout(() => {
          this.changewidth(e)
        }, 0);
      }
    },
    play(e) {
      let url = e.target.parentElement.getAttribute('data-url')
      this.src = url
      console.log(this.src)
      let video = document.querySelector('video')
      video.oncanplay = () => {
        video.play()
      }
    },
  },
  mounted() {
    this.getdata()
  }
}
</script>
<style lang="scss" scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.player {
  position: relative;
  width: 60vw;
  height: 70vh;
  border: 1px solid #355665;
  border-right: none;
  background-color: transparent;
  svg {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: -1
  }
  video {
    width: 100%;
    height: 70vh;
  }
}


.list {
  width: 30vw;
  height: 70vh;
  background-color: #3d93c4;
  border: 1px solid #355665;
  border-left: none;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  overflow: auto;
  overflow-x: hidden;
  .item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 10%;
    font-size: 1.8vw;
    background-color: #47b9de;
    border-bottom: 1px solid #475df9;
    &>div {
      height: 100%;
      font-size: 1.5vw;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .user {
      background-color: rgba(111, 219, 205, 0.79);
      transition: width 1s linear;
    }
    .iconSpan {
      width: 9%;
      background-color: #556f72;
      box-shadow: 0 0 2px 3px #274d52 inset;
    }
    .filename {
      background-color: rgba(111, 219, 205, 0.79);
      transition: width 1s linear;
    }
  }
}

.widthreduce {
  width: 0;
  overflow: hidden;
}

.widthadd {
  width: 90%;
  overflow: hidden;
}
</style>



