import Vue from 'vue';
import Router from 'vue-router';
import chat from '@/components/chat';
import index from '@/components/index';
import video from '@/components/video';
Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/video',
            component: video
        },
        {
            path: '/upload',
            component: index
        },
        {
            path: '/chat',
            component: chat
        }
    ]
});



