<template>
  <div class='content'>
    <div class='container'>
      <div v-for='x in messageList' v-bind:class='x.class'>
        <div v-if='x.class!=="info"' v-bind:style="{backgroundColor:x.color}"></div>
        <div v-bind:style="{backgroundColor:x.color}" v-on:mouseenter='showInfo' :data-id='x.id'>{{x.message}}</div>
      </div>
    </div>
    <div class='input'>
      <input type="text " placeholder="输入" v-model='inputMessage' v-on:keyup.enter='sendMessage'>
    </div>
  </div>
</template>
<script>
//到时候修改port
export default {
  name: 'chat',
  data() {
    return {
      inputMessage: '',
      messageList: [],
      websocket: null,
      users: {},
      locked: false,
      colors: ['pink', 'orangered', 'red', 'cadetblue', 'lightgoldenrodyellow', 'lightcoral', '#80a6f0', '#d25757', 'rgb(43, 199, 121)'],
      port: 8000 || location.port,
    }
  },
  methods: {
    render(data) {
      data = JSON.parse(data)
      let r = {}
      console.log(data);
      switch (Number(data.code)) {
        case 1:
          [r.class, r.id, r.message, r.color] = ['myself', data.id, data.message, this.users[data.id] && this.users[data.id].color || void console.log('no defined')]
          break
        case 2:
          [r.class, r.message] = ['info', `${data.id}进入聊天室`]
          if (data.id in this.users)
            break
          this.setcolor(data.id)
          break
        case 3:
          if (!(data.id in this.users)) { console.log('ss'); this.setcolor(data.id) }
          [r.class, r.id, r.message, r.color] = ['other', data.id, data.message, this.users[data.id] && this.users[data.id].color || void console.log('no defined')]
          break
        case 4:
          [r.class, r.message] = ['info', `${data.id}离开聊天室`]
          break
      }
      this.messageList.push(r)
    },
    setcolor(id) {
      this.users[id] = { color: this.colors[Math.floor(Math.random() * 10)] }
    },
    showInfo(e) {
      if (this.locked || !e.target.getAttribute('data-id')) {
        return
      }
      this.locked = true
      let id = e.target.getAttribute('data-id')
      let span = document.createElement('span')
      span.innerHTML = `<br/>消息来自${id}  `
      e.target.appendChild(span)
      setTimeout(() => {
        this.locked = false;
        e.target.removeChild(span)
      }, 1000);
    },
    ws() {
      this.websocket = new WebSocket('ws://' + `${location.hostname}:${this.port}`)
    },
    sendMessage() {
      if (this.websocket) {
        this.websocket.send(this.inputMessage)
        this.inputMessage = '';
      } else {
        this.ws()
      }
    },
    getColor(id) {
      return this.users[id]['id']
    }
  },
  mounted() {
    this.ws()
    this.websocket.onmessage = message => {
      this.render(message.data)
    }
    this.websocket.onclose = () => {
      console.log('close')
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  width: 70vw;
  height: 70vh;
  margin: auto;
  overflow: scroll;
  background-color: rgba(85, 236, 255, 0.19);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  box-shadow: 0 0 4px 1px rgba(0, 226, 255, 0.49) inset;
}

.input {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  width: 70vw;
  height: 7vh;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #69d0f3;
  input {
    text-align: center;
    width: 70%;
    height: 90%;
    font-size: 2.1vw;
  }

  span {
    display: block;
    color: #37502c;
    border-radius: 3px;
    background-color: #88e1ec;
    padding: 2px;
    margin: 2px;
    &:hover {
      box-shadow: 0 0 1px 2px #69d5ff inset
    }
  }
}
</style>
<style lang="scss" >
.info {
  padding: 2px;
  width: 50%;
  height: 4vh;
  margin: 10px auto;
  opacity: 0.7;
  border-radius: 10px;
  background-color: yellow;
  text-align: center;
  font-size: 1vw;
}

.other {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  align-items: center;
  padding: 4px;
  width: 50%;
  padding: 4px;
  border-radius: 6px;
  &>div:nth-child(1) {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: attr(data-color);
  }
  &>div:nth-child(2) {
    position: relative;
    width: 70%;
    border-radius: 10px;
    background-color: attr(data-color);
    padding: 10px;
  }
}

.myself {
  margin-left: 50%;
  display: flex;
  flex-flow: row-reverse;
  justify-content: space-around;
  align-items: center;
  width: 50%;
  padding: 4px;
  border-radius: 6px;
  border-radius: 10px;
  &>div:nth-child(1) {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: attr(data-color, color);
  }
  &>div:nth-child(2) {
    width: 70%;
    border-radius: 10px;
    background-color: attr(data-color, color);
    text-align: center;
    padding: 10px;
  }
}
</style>




