<template>
  <div>
    <header>
      <div id='open'></div>
      <div class='list'>
        <router-link tag="div" class="tab-item" to="/upload">
          <span class="tab-link">上传文件</span>
        </router-link>
        <router-link tag="div" class="tab-item" to="/video">
          <span class="tab-link">电影</span>
        </router-link>
        <div>
          <a style='text-decration:' href='/'>主页
          </a>
        </div>
        <router-link tag="div" class="tab-item" to="/chat">
          <span class="tab-link">聊天</span>
        </router-link>
      </div>
    </header>
    <aside>
      <div id='close'>
        close
      </div>
      <section>
        <div class='title' id='audio'>audio</div>
        <ul class='list' id='audio_l'></ul>
        <div class='title' id='video'>video</div>
        <ul class='list' id='video_l'></ul>
      </section>
      <svg width="100%" height="100%" preserveAspectRatio="none" viewBox='0,0,100,100'>
        <path d='M0,0 L0,100 Q-50,-50 100,0' fill='rgba(172, 223, 255,1)' />
      </svg>
    </aside>
  </div>
</template>
<script>
export default {
  name: 'tmp',
  data() {
    return {}
  },
  mounted() {
    /* upload file*/

    let log = console.log;
    function $(str) {
      let a;
      if (typeof str === 'string') {
        a = document.querySelector(str);
      } else {
        a = str;
      }
      let b = function() {
        this.obj = a;
      };
      b.prototype = {
        ev(str, fn) {
          this.obj.addEventListener(str, fn);
        },
        s(obj) {
          for (let x in obj) {
            this.obj.style[x] = obj[x];
          }
        },
        html(h) {
          this.obj.innerHTML = h.innerHTML;
        },
        setA(attr, value) {
          this.obj.setAttribute(attr, value);
        }
      };
      return new b();
    }

    (() => {
      $('#audio').ev('click', () => {
        fetch('/media/audio')
          .then(data => data.json())
          .then(data => {
            let ul = document.createElement('ul');
            for (let x in data) {
              let name = x;
              let li = document.createElement('li');
              li.setAttribute('data-id', data[x].id);
              li.innerText = name;
              ul.appendChild(li);
            }
            $('#audio_l').html(ul);
          });
      });
      $('#video').ev('click', () => {
        fetch('/media/video')
          .then(data => data.json())
          .then(data => {
            console.log(data);
            let ul = document.createElement('ul');
            for (let x in data) {
              let name = x;
              let li = document.createElement('li');
              li.setAttribute('data-id', data[x].id);
              li.innerText = name;
              ul.appendChild(li);
            }
            $('#video_l').html(ul);
          });
      });
    })();

    (() => {

      //锁住动画
      let lock = false;

      //中央信道
      let message = [];

      //开始终结状态
      function showUp(up) {
        if (up) {
          $('#close').s({ width: '100%' });
          $('aside section').s({ width: '100%' });
        } else {
          $('#close').s({ width: '0' });
          $('aside section').s({ width: '0' });
        }
      }

      //svg出现
      function fadeIn() {
        let x = -50,
          y = -50;
        let raf;

        function update() {
          if (!(x > 100)) {
            x += 1;
            y += 1;
          } else {
            y += 2;
          }
          $('aside svg path').setA('d', 'M0,0 L0,100 Q' + x + ',' + y + ' 100,0');
          if (!(y > 350)) {
            raf = requestAnimationFrame(update);
          } else {
            cancelAnimationFrame(raf);
            //解锁
            lock = false;
            for (let x of message) {
              x.emit();
            }
          }
        }
        update();
        setTimeout(function() {
          showUp(true);
        }, 1500);
      }

      //svgo消失
      function fadeOut() {
        message = [];
        let x = 100,
          y = 250;
        let raf;

        function update() {
          if (!(x > -100)) {
            x -= 1;
            y -= 1;
          } else {
            x -= 2;
            y -= 2;
          }
          $('aside svg path').setA('d', 'M0,0 L0,100 Q' + x + ',' + y + ' 100,0');
          if (!(y < -20)) {
            raf = requestAnimationFrame(update);
          } else {
            cancelAnimationFrame(raf);
            $('#open').s({ transform: 'rotate(0) scale(1)' });
            $('aside').s({ width: '0' });
          }
        }
        update();
      }

      //打开
      $('#open').ev('click', function() {
        if (!lock) {
          lock = true;
          $(this).s({ transform: 'rotate(360deg) scale(0)' });
          setTimeout(function() {
            $('aside').s({ width: '17vw' });
            fadeIn();
          }, 1000);
        }
      });
      //关闭
      $('#close').ev('click', function() {
        showUp(false);
        if (!lock) {
          fadeOut();
        } else {
          //放入待定事件,等待处理
          message.push({
            emit: function() {
              fadeOut();
            }
          });
        };
      })
    })()
  }
}
</script>
<style scoped>
/*header*/

header {
  display: flex;
  width: 100%;
  height: 10vw;
  background-color: #00ccff;

  justify-content: space-between;
  align-items: center;
}

#open {
  width: 10vw;
  height: 10vw;
  border-bottom-right-radius: 100%;
  background-color: #00efff;
  transition: all 1s linear;
}

header .list {
  display: flex;
  width: calc(100% - 10vw - 20%);
  height: 100%;
  justify-content: space-around;
  align-items: center;
}

header .list a {
  text-decoration: none;
}











/* aside*/

aside {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 0;
  height: 100vh;
}

aside svg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

#close {
  display: flex;
  overflow: hidden;
  width: 0;
  height: 5%;
  background-color: #009dff;
  transition: all 0.7s linear;

  justify-content: center;
  align-items: center;
}

aside .title {
  padding: 5px;
  background-color: #00c0ff;
  font-size: 3vw;
}

aside ul {
  margin: 2vw auto;
  padding-left: 40px;
  list-style: none;
  font-size: 1.7vw;
  overflow: auto;
}

aside section {
  overflow: hidden;
  margin: 20vh auto 0;
  width: 0;
  transition: all 1s linear;
}
</style>



