<template>
  <div>
    <upload>
    </upload>
    <canvas id='drop'>
    </canvas>
  </div>
</template>
<script>
import upload from '@/components/upload'
export default {
  name: 'index',
  methods: {
    init() {
      //兼容性
      window.requestAF = function(callback) {
        return window.requestAnimationFrame(callback) ||
          window.webkitRequestAnimationFrame(callback) ||
          window.mozRequestAnimationFrame(callback) ||
          function(callback) {
            if (typeof callback === 'function') setTimeout(callback, 1000 / 60);
            else throw "this is not a function";
          }
      };

      let can = document.getElementById('drop');
      let [w, h] = [can.width, can.height] = [window.innerWidth, window.innerHeight];
      let wRange = [[30, 60], [w - 60, w - 30]];
      let hRange = [50, 70];
      let bottRange = [h - 200, h - 125];
      window.onresize = function() {
        [w, h] = [can.width, can.height] = [window.innerWidth, window.innerHeight];
      };
      function random(...range) {
        if (range.length === 1) {
          return random(0, ...range);
        }
        else if (range.length === 2) {
          return Math.random() * (range[1] - range[0]) + range[0];
        }
        throw 'arguments must less than 2'
      }

      let ctx = can.getContext('2d');

      function chooseOne(a, b) {
        let c = Math.random();
        if (c > 0.5) {
          return a
        } else {
          return b
        }
        return
      }

      class Drop {
        init() {
          this.x = chooseOne(random(wRange[0][0], wRange[0][1]), random(wRange[1][0], wRange[1][0]));
          this.y = random(hRange[0], hRange[1]);
          this.v_y = random(3, 6);
          this.bottom = random(bottRange[0], bottRange[1]);
          this.r = random(50, 70);
          this.rv = random(1, 2);
          this.rMax = random(150, 200);
          this.a = '.9';
        }
        draw() {
          if (this.y < this.bottom) {
            ctx.fillStyle = 'rgba(0,255,255,.9)';
            ctx.fillRect(this.x, this.y, 5, 20);
          }
          else {
            ctx.beginPath();
            ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2, true);
            ctx.strokeStyle = 'rgba(0,255,255,' + this.a + ')';
            ctx.stroke();
          }
          this.update();
        }
        update() {
          if (this.y < this.bottom) {
            this.y += this.v_y;
          }
          else if (this.r <= this.rMax)
            this.r += this.rv;
          if (this.r > this.rMax - 50) {
            this.a -= .1;
            if (this.r >= this.rMax) {
              this.init();
            }
          }
        }
      }
      let dropsArray = [];
      let count = 15;
      //切记,这里的雨点要先生成再绘制
      for (let i = 0; i < count; i++) {
        let dropTmp = new Drop();
        dropTmp.init();
        dropsArray.push(dropTmp);
      }
      function dropMove() {
        //清除再继续绘制
        ctx.fillStyle = 'rgba(255,255,255,0.1)';
        ctx.fillRect(0, 0, w, h);
        for (let tmp of dropsArray) {
          tmp.draw();
        }
        window.requestAF(dropMove);
      }
      dropMove();
    }
  },
  mounted() {
    this.init();
  },
  components: {
    upload
  }
}
</script>
<style>
canvas {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: -1;
}
</style>



