<template>
  <div id="app">
    <tab></tab>
    <keep-alive>
      <router-view class='z'></router-view>
    </keep-alive>
  </div>
</template>

<script>
import tab from '@/components/tab'
export
  default {
    name: 'app',
    components: {
      tab
    }
  }
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.z {
  margin-top: 20px;
}
</style>



